                    xAxis.push(res[i].fupinjieguo);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].fupinjieguo
                    })
                }
                var option = {};
				let titleObj = this.gauge.title
				titleObj.text = '帮扶结果统计'
				
				for(let x=0;x<pArray.length;x++){
					pArray[x] = Object.assign(pArray[x], {
						title: {
							offsetCenter: [String((-160 + (x) * 60) + '%'), '80%']
						},
						detail: {
							offsetCenter: [String((-160 + (x) * 60) + '%'), '105%']
						}
					})
				}
				let seriesObj = {
					data: pArray,
					type: 'gauge',
				}
				seriesObj = Object.assign(seriesObj, this.gauge.series)
				const gridObj = this.gauge.grid
				option = {
					backgroundColor: this.gauge.backgroundColor,
					color: this.gauge.color,
				    title: titleObj,
				    tooltip: this.gauge.tooltip,
				    series: [seriesObj],
					grid: gridObj,
				}
                // 使用刚指定的配置项和数据显示图表。
                fupinxinxiChart1.setOption(option);
				

                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    fupinxinxiChart1.resize();
				},false);
            }
        });
      })
    },

    fupinxinxiChat5() {
		this.$nextTick(()=>{
			echarts.registerMap("china", chinaJson);
			var fupinxinxiChart5 = echarts.init(document.getElementById("fupinxinxiChart5"),'macarons');

			this.$http({
				url: `fupinxinxi/value/sheng/chengyuanshuliang`,
				method: "get",
			}).then(({ data }) => {
				if (data && data.code === 0) {
					let res = data.data;
					let xAxis = [];
					let yAxis = [];
					let pArray = []
					for(let i=0;i<res.length;i++){
					    xAxis.push(res[i].sheng);
					    yAxis.push(parseFloat((res[i].total)));
					    pArray.push({
					        value: parseFloat((res[i].total)),
					        name: res[i].sheng
					    })
					}
					let titleObj = this.map.title
					titleObj.text = '贫困人数统计'
					let seriesObj = [{
						type: "map",
						map: 'china',
						data: pArray
					}]
					for (let i in seriesObj) {
					  seriesObj[i] = Object.assign(seriesObj[i] , this.map.series)
					}
					const gridObj = this.map.grid
					var option = {
						backgroundColor: this.map.backgroundColor,
						title: titleObj,
						tooltip: this.map.tooltip,
						visualMap: this.map.visualMap,
						series: seriesObj,
						grid: gridObj,
						legend: this.map.legend
					}
					fupinxinxiChart5.setOption(option);
					//根据窗口的大小变动图表
					window.addEventListener('resize', () => {
						fupinxinxiChart5.resize();
					},false);
				}
			})
		})
	},





  }
};
</script>
